<template>
    <div class="HelpCenter">
      <HomePageTop :TopTitle="TopTotle" class="bg_fff"></HomePageTop>
      <div class="searchInput">
        <p>
          <i class="iconfont">&#xe61a;</i>
          <input type="text" placeholder="请输入您想要了解问题的关键词">
        </p>
      </div>
<!--      //具体问题-->
<!--      <div class="SpecificIssue both" v-for="(data,index) in IssueContent">-->
      <div class="SpecificIssue both" v-for="(data,index) in IssueContent">
        <div class="IssueLeft" @click="pack(data,index)">
          <img :src=data.imgUrl   alt="">
          <p>{{data.IssueContent.left}}</p>
          <i :class="showtrue==index?'bg_red':'bg_witeh'"></i>
        </div>
        <div class="IssueRight">
          <p v-for="(Home,i) in data.homebox" v-if="showtrue==index?true:i<IssueIndex" @click="particulars(data,i)">{{Home}}</p>
        </div>
      </div>
      <div class="SpecificIssue both mb40"  v-for="(data,index) in IssueContent1">
        <div class="IssueLeft">
          <img :src=data.imgUrl   alt="">
          <p>{{data.IssueContent.left}}</p>
        </div>
        <div class="IssueRight" >
          <p v-for="(Home,i) in data.homebox" v-if="showtrue==index?true:i<IssueIndex" @clicl="particulars(data,i)">{{Home}}</p>
        </div>
      </div>
    </div>
</template>

<script>
    import Vue from "vue"
    import HomePageTop from "../common/HomePageTop";

    export default {
        name: "HelpCenter",
        components: {HomePageTop},
        data() {
            return {
              //像子组件传值
              TopTotle: { name: "帮助中心",left: true},
              //定义变量为下标进行跟v-if判断
              IssueContent:[
                {IssueContent:{left:"热门问题"},routers:false,imgUrl:require("../../assets/img/common/pitch.png"),homebox:[ "热门问题","请问贵司1.6mm板厚的4层板和6...","机械层和keepout层混用而到导致...","任何计算交期时间？","周六18：00之前发版，周一能收...","什么是常规工艺订单？","上传文件后，是先审核，还是要付...","如何获得积分"],},
                {IssueContent:{left:"快速入门"},routers:true,imgUrl:require("../../assets/img/common/pitch.png"),homebox:[ "快速入门","PCB下单流程","SMT下单流程","BOM下单流程"],HelpParticulars:["一个账户可以绑定两个开票公司资料吗？","是否可以走公对公付款，公司现在财务要求员...","上传文件后，是先审核，还是要付款后再审核"],},
                {IssueContent:{left:"常见问题"},routers:true, imgUrl:require("../../assets/img/common/pitch.png"),homebox:[ "常见问题","支付问题","账号问题","订单问题"],HelpParticulars:["一个账户可以绑定两个开票公司资料吗？","是否可以走公对公付款，公司现在财务要求员...","上传文件后，是先审核，还是要付款后再审核"],},
              ],
              IssueContent1:[
                {IssueContent:{left:"联系我们"},routers:false, imgUrl:require("../../assets/img/common/pitch.png"),homebox:[ "联系电话","在线客服","邮箱"],},
              ],
              IssueIndex:3,
              showtrue:"",
            }
        },
        methods: {
          pack(a,i){
            console.log(a, i);
            this.showtrue=i

          },
          particulars(data,index){
            console.log(data, index);
            if(data.routers==true){
              this.$router.push({
                name:"HelpParticulars",
                params:{Home:data.HelpParticulars,name:data.IssueContent.left}
              })
            }else{
              this.$router.push({
                path:"HelpContent"
              })
            }
          },
        },
        created() {

        }
    }

</script>

<style lang="less">
  .HomePageTop .Head{
    background: #fff;

  }
  .bg_witeh{
    background: url("../../assets/img/common/Chevronbottom.png") no-repeat center;
    background-size:100%;
  }
  .bg_red{
    background: url("../../assets/img/common/Chevrontop.png") no-repeat center;
    background-size:100%;
  }
  .searchInput{
    width: 100%;
    height: 1.02rem;
    background: #fff;
    padding: .18rem 0;
    border-bottom: 1px solid #E3E4E8;
    p{
      position: relative;
      background: #F0F2F5;
      width: 6.9rem;
      margin: 0 auto;
      height: .66rem;
      padding: .13rem 0;
      padding-left: .84rem;
      i{
        display: inline-block;
        width: .4rem;
        height: .4rem;
        position: absolute;
        left: .24rem;
        top: .13rem;
        color: #CCCCCC;
        font-size: .36rem;
      }
      input{
        border: none;
        vertical-align: middle;
        background: #F0F2F5;

      }
    }

  }
  .SpecificIssue{
    margin-top: .24rem;
    background: #fff;
    .IssueLeft{
      float: left;
      width: 1.54rem;
      height: auto;
      padding-top: .32rem;
      text-align: center;
      img{
        width: .4rem;
        height: .4rem;
        margin-bottom: .22rem;
      }
      p{
        font-size: .24rem;
        color: #333;
        text-align: center;
        margin-bottom: .15rem;
      }
      i{
        display: inline-block;
        width: .16rem;
        height: .26rem;
        /*background: #676a7d;*/
      }

    }
    .IssueRight{
      padding-right: .3rem;
      float: left;
      width: 5.95rem;
      p{
        height: .94rem;
        line-height: .94rem;
        padding-left: .24rem;
        font-size: .34rem;
        color: #333;
        border-left: 1px solid #E3E4E8;
        border-top: 1px solid #E3E4E8;
        white-space:nowrap;
        overflow:hidden;
        text-overflow:ellipsis;
        width: 100%;
      }
    }
  }
  .IssueRight p:nth-child(1){
    border-top: none;

  }

</style>



